import React from "react";
import PropTypes from "prop-types";
import PageTreeNode from "./PageTreeNode";

export default class PageTreeDraggable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dragging: this.initDragging()
    };
  }

  initDragging() {
    return {
      id: null,
      x: null,
      y: null,
      w: null,
      h: null,
      scrollTop: null,
      scrollLeft: null
    };
  }

  getDraggingDom() {
    var tree = this.props.tree;
    var dragging = this.state.dragging;
    if (dragging && dragging.id) {
      var draggingIndex = tree.getIndex(dragging.id);
      var draggingStyles = {
        top: dragging.y,
        left: dragging.x,
        width: dragging.w
      };

      return (
        <div className="draggable" style={draggingStyles}>
          <PageTreeNode
              tree={tree}
              index={draggingIndex}
              paddingLeft={this.props.paddingLeft}
          />
        </div>
      );
    }

    return null;
  }

  render() {
    const { tree, dir, locale } = this.props;
    var dragging = this.state.dragging;

    if (!tree) {
      return (
        <div className="page-tree">
          {this.getDraggingDom()}
        </div>
      );
    } else {
      var root = tree.getIndex(1);
      return (
        <div className="page-tree">
          {this.getDraggingDom()}
          <PageTreeNode
            tree={tree}
            index={root}
            key={root.id}
            paddingLeft={this.props.paddingLeft}
            addChild={id => this.addChild(id)}
            onDragStart={(id, dom, e) => this.dragStart(id, dom, e)}
            onCollapse={nodeId => this.toggleCollapse(nodeId)}
            updatePage={(idx, attrs) => this.updatePage(idx, attrs)}
            dragging={dragging && dragging.id}
            dir={dir}
            locale={locale} />
        </div>
      );
    }
  }

  addChild(parent) {
    let newNode = {
      name: "",
      status: 0,
      editing: true,
      children: [],
      published_at: new Date(),
      pinned: false,
      locale: parent.node.locale,
      parent_page_id: parent.node.id
    };
    this.props.addChild(parent.id, newNode);
  }

  prevAddButtonCount(tree, index) {
    let count = 0;
    let parentNodes = [];
    let pointer = tree.getIndex(index.parent);
    while (pointer) {
      parentNodes.push(pointer);
      pointer = tree.getIndex(pointer.parent);
    }

    pointer = index;

    pointer = tree.getNodeByTop(index.top - 1);
    while (pointer) {
      if (
        parentNodes.indexOf(pointer) == -1 &&
        !pointer.node.collapsed &&
        pointer.node.children.filter(p => p.status != 4).length > 0
      ) {
        count += 1;
      }
      pointer = tree.getNodeByTop(pointer.top - 1);
    }

    return count;
  }

  scrollOffset() {
    let dragging = this.state.dragging;
    return {
      top: document.body.scrollTop - dragging.scrollTop,
      left: document.body.scrollLeft - dragging.scrollLeft
    };
  }

  drag(e) {
    if (this._start) {
      var distance = Math.abs(e.clientX - this._offsetX) +
                     Math.abs(e.clientY - this._offsetY);
      if (distance >= 15) {
        this.setState({
          dragging: this.dragging
        });
        this._start = false;
      } else {
        return null;
      }
    }

    var tree = this.props.tree;
    var dragging = this.state.dragging;
    var paddingLeft = this.props.paddingLeft;
    var newIndex = null;
    var index = tree.getIndex(dragging.id);
    var collapsed = index.node.collapsed;

    var _startX = this._startX;
    var _startY = this._startY;
    var _offsetX = this._offsetX;
    var _offsetY = this._offsetY;

    var pos = {
      x: _startX + e.clientX - _offsetX + this.scrollOffset().left,
      y: _startY + e.clientY - _offsetY + this.scrollOffset().top
    };
    dragging.x = pos.x;
    dragging.y = pos.y;

    var diffX = dragging.x - paddingLeft/2 - (index.left-2) * paddingLeft;
    var diffY = dragging.y - dragging.h/2 - (index.top-2 + this.prevAddButtonCount(tree, index)) * dragging.h;

    if (diffX < 0) {
      // left
      if (index.parent && !index.next) {
        newIndex = tree.move(index.id, index.parent, "after");
      }
    } else if (diffX > paddingLeft) {
      // right
      if (index.prev) {
        var prev = tree.getIndex(index.prev);

        if (!prev.node.leaf && !prev.node.collapsed) {
          newIndex = tree.move(index.id, index.prev, "append");
        }
      }
    }

    if (newIndex) {
      index = newIndex;
      newIndex.node.collapsed = collapsed;
      dragging.id = newIndex.id;
    }

    if (diffY < (0 - dragging.h * 0.5)) {
      // up
      var above = tree.getNodeByTop(index.top-1);
      newIndex = tree.move(index.id, above.id, "before");
    } else if (diffY > dragging.h * 1.5) {
      // down
      let below = index.next ?
                  tree.getIndex(index.next) :
                  tree.getNodeByTop(index.top + index.height);

      if (below && below.parent !== index.id) {
        if (below.children && below.children.length && !below.node.collapsed) {
          newIndex = tree.move(index.id, below.id, "prepend");
        } else {
          newIndex = tree.move(index.id, below.id, "after");
        }
      }
    }

    if (newIndex) {
      newIndex.node.collapsed = collapsed;
      dragging.id = newIndex.id;
    }

    this.setState({ dragging: dragging });
  }

  dragStart(id, dom, e) {
    // Only drag on left click
    if (e.button !== 0) {
      return;
    }

    this.dragging = {
      id: id,
      w: dom.offsetWidth,
      h: dom.offsetHeight,
      x: dom.offsetLeft,
      y: dom.offsetTop,
      scrollTop: document.body.scrollTop,
      scrollLeft: document.body.scrollLeft
    };

    this._startX = dom.offsetLeft;
    this._startY = dom.offsetTop;
    this._offsetX = e.clientX;
    this._offsetY = e.clientY;
    this._start = true;

    this._dragListener = (e) => this.drag(e);
    this._dragEndListener = () => this.dragEnd();

    window.addEventListener("mousemove", this._dragListener);
    window.addEventListener("mouseup", this._dragEndListener);
  }

  dragEnd() {
    if (!this._start) {
      this.props.updateTree(this.props.tree);
      this.props.movedPage(this.state.dragging.id);
    }

    this.setState({
      dragging: this.initDragging()
    });

    window.removeEventListener("mousemove", this._dragListener);
    window.removeEventListener("mouseup", this._dragEndListener);
  }

  toggleCollapse(nodeId) {
    this.props.toggleCollapsed(nodeId);
  }

  updatePage(index, attributes) {
    this.props.updatePage(index.id, attributes);
  }
}

PageTreeDraggable.defaultProps = {
  paddingLeft: 15
};

PageTreeDraggable.propTypes = {
  tree: PropTypes.object,
  addChild: PropTypes.func,
  movedPage: PropTypes.func,
  toggleCollapsed: PropTypes.func,
  paddingLeft: PropTypes.number,
  updatePage: PropTypes.func,
  updateTree: PropTypes.func,
  locale: PropTypes.string,
  dir: PropTypes.string
};
